import $ from 'jquery'
import { Avatar, Button } from 'antd';
import { useNavigate } from 'react-router-dom';
import 'antd/dist/antd.css';
import axios from 'axios';
import { useState } from 'react';
import './Login.css'

const Login = () => {
  const navi = useNavigate()
  const [log, setlog] = useState({} as any)
  function seize() {
    var info = $('form.login').serialize().split('&').map(it => {
      return it.split('=')[1]
    })
    console.log(info)
    axios.post('/account/login', {
      name: info[0],
      password: info[1]
    }).then((res: any) => { 
      setlog(res.data.result)
      setTimeout(() => {
        navi('/user')
      }, 2000)
    })
  }
  return (
    <div className='login'>
      {
        log.name !== undefined
        &&
        <div className="confirm">
          <p>欢迎登录！正在前往往用户界面...</p>
          <div className='try_login'>
            {/* <span className="avatar">{log.avatar === null ? 'default' : log.avatar}</span> */}
            <Avatar size='large' src={log.avatar === null ? '/upload/default.jpg' : log.avatar} />
            <span className="name">{ log.name }</span>
          </div>
        </div>
      }
      <h1>Login, please</h1>
      <form action="" onSubmit={() => false} className="login">
        <div>
          用户名： <input type="text" name='username' />
        </div>
        <div>
          密码： <input type="password" name='password' />
        </div>
        <Button type="primary" size='large' onClick={seize}>登录</Button>
      </form>
      <h4>还未注册？ 请先{' '}
        <Button type="primary" size='small' onClick={() => { navi('/register') }}>注册</Button>{' '}
        或者
        <Button type="primary" size='small' onClick={() => { navi('/') }}>返回主页</Button>
      </h4>
    </div>
  )
}

export default Login