import $ from 'jquery'
import { Avatar, Button } from 'antd';
import { useNavigate } from 'react-router-dom';
import 'antd/dist/antd.css';
import { useState } from 'react';
import axios from 'axios';
import './Register.css'
const Register = () => {
  const [avatar, setAvatar] = useState('')
  const navi = useNavigate()
  async function seize(e: React.FormEvent<HTMLFormElement>) {
    e.preventDefault()
    var info = {} as any
    $('form.register').serialize().split('&').forEach(it => {
      var [key, val] = it.split('=')
      info[key] = decodeURIComponent(val)
    })
    console.log(info)
    if (info.password !== info.confirm) {
      alert('两次密码不相符！')
    } else {
      try {
        await axios.post('/account/register', {
          name: info.username,
          password: info.password,
          email: info.email,
          avatar: avatar,
        })
        navi('/login')
      } catch (e:any) {
        // console.log(e)
        alert(e.response.data.msg)
      }
    }
  }
  async function handleFileSelect(e: React.ChangeEvent<HTMLInputElement>) {
    if (e.target.files && e.target.files.length > 0) {
      var file = e.target.files[0]
      var fd = new FormData()
      fd.append('avatar', file)
      var res = await axios.post('/upload', fd)
      console.log(res)
      setAvatar(res.data[0])
    }
  }
  
  return (
    <div className='register'>
      <h1>Register, please</h1>
      <form action="" onSubmit={seize} className="register">
        <span>
          用户名： <input type="text" required name='username' maxLength={8} />
        </span>
        <span>
          密码： <input type="password" required name='password' maxLength={16} />
        </span>
        <span>
          确认密码： <input type="password" required name='confirm' />
        </span>
        <span>
          邮箱： <input type="email" required name='email' />
        </span>
        <span>
          头像： <input type="file" name='avatar' accept="image/*" onChange={handleFileSelect} />
        </span>
        <span style={{
          justifyContent: 'center'
        }}>
          <Avatar size={100} src={avatar === '' ? '/upload/default.jpg' : avatar} />
        </span>
        <button id='register' hidden>注册</button>
        <Button type="primary" onClick={() => {
          // @ts-ignore
          document.querySelector('#register').click()
        }} size='large'>注册</Button>
        <Button type="primary" onClick={() => {
          navi('/')
        }} size='large'>回到主页</Button>
      </form>
    </div>
  )
}

export default Register