// import { Spin } from 'antd';
import { useEffect, useState } from 'react';
import { NavLink, useNavigate } from 'react-router-dom';
import { Avatar } from 'antd';
import './User.css'
const axios = require('axios');

const User = () => {
  const [vote, setvote] = useState(null)
  const [userInfo, setInfo] = useState('')
  var navi = useNavigate()
  useEffect(() => {
    axios('/account/current-user').then((res: any) => {
      console.log('用户信息', res)
      setInfo(res.data.result)
      // 登录成功后请求该用户所创建的投票
      axios('/vote').then((res: any) => {
        console.log('投票信息', res)
        setvote(res.data.result)
      })
    }).catch((e:any) => {
      // console.log('登录失败', e)  e.response.data
      if (e.response.data.code < 0) {
        setInfo(e.response.data.msg)
      } else {
        throw(e)
      }
    })
  }, [])
  function logout() {
    axios('/account/logout').then(() => {
      setInfo('对不起，您还未登录。')
      setvote(null)
    })
  }

  function setClipboard(text:string) {
    const type = "text/plain";
    const blob = new Blob([text], { type });
    const data = [new ClipboardItem({ [type]: blob })];
   
    navigator.clipboard.write(data).then(
        function () {
        /* success */
        alert('复制地址成功！')
        },
        function () {
          /* failure */
        alert('复制地址失败！')
        }
    );
  }

  function deleteVote(num:number) {
    axios.delete(`/vote/${num}`).then(() => {
      alert('删除成功！')
      setvote((state:any) => {
        return state.filter((it:any) => it.voteId !== num)
      })
    }).catch(() => {
      alert('删除失败！')
    })
  }
  return (
      <div className="myvote">
        {
          userInfo === ''
          &&
          <span>loading...</span>
        }
        {
          (typeof userInfo === 'string' && userInfo !== '')
          &&
          <h2>{ userInfo }请先 <NavLink to='/login'>登录</NavLink></h2>
        }
        {
          (typeof userInfo === 'object')
          &&
          <div className='userInfo'>
            {/* @ts-ignore */}
            <Avatar size={64} src={userInfo.avatar === null ? '/upload/default.jpg' : userInfo.avatar} />
            <>
              {/* @ts-ignore */}
              <span>NAME: { userInfo.name }</span><span>ID: { userInfo.userId }</span>
            </>
            <button onClick={ logout }>登出</button>
          </div>
      }
      <h2>MY VOTES</h2>
        {
          Array.isArray(vote)
          &&
          <div className='userVotes'>
            {
              // @ts-ignore
              vote.map((it,idx) => {
                return <div key={idx} >
                  <input type="radio" hidden name='selectVote' id={ 'myvote-' + it.voteId } />
                  <label htmlFor={'myvote-' + it.voteId}>{it.title}</label>
                  <div className="selectOption">
                    <button onClick={() => {navi(`/vote/${it.voteId}`)}}>查看</button>
                    <button onClick={() => {setClipboard(`http://localhost:3000/#/vote/${it.voteId}`)}}>分享</button>
                    <button onClick={() => {deleteVote(it.voteId)}}>删除</button>
                  </div>
                </div>
              })
            }
          </div>
        }
      </div>
  )
}

export default User