import './App.css';
import { HashRouter, Route, Routes } from 'react-router-dom';
import Home from './cpts/Home';
import Create from './cpts/Create';
import User from './cpts/User';
import Vote from './cpts/Vote';
import Login from './cpts/Login';
import Register from './cpts/Register';
import TheVote from './cpts/TheVote';



function App() {
  return (
    <div className="App">
      <HashRouter>
        <Routes>
          <Route path='/' element={<Home />}>
            <Route index element={<Create />} />
            <Route path='create' element={<Create />} />
            <Route path='user' element={<User />} />
          </Route>
          <Route path='newVote/:style' element={<Vote />} />
          <Route path='vote/:id' element={<TheVote />} />
          <Route path='login' element={<Login />} />
          <Route path='register' element={<Register />} />
        </Routes>
      </HashRouter>      
    </div>
  );
}

export default App;
