import axios from "axios"
import { useMemo, useState } from "react"
import { useNavigate, useParams } from "react-router-dom"
import { Avatar, Button, Modal, Spin } from "antd"
import './TheVote.css'
import $ from 'jquery'
import { Progress } from 'antd';
import { ExclamationCircleOutlined } from '@ant-design/icons';
const { confirm } = Modal;
interface dbSearch {
  vote: any,
  options: any[],
  userVotes: any[]
}
const TheVote = () => {
  var param = useParams()
  var id = param.id
  var navi = useNavigate()
  //登陆情况
  var cookieMatch = document.cookie.match(/loginUser=.*\./)
  var currentUser = cookieMatch !== null ? cookieMatch[0] : null
  const [voteinfo, setInfo] = useState({} as dbSearch)
  //建立本地客户端，实时更新
  const client = useMemo(() => {
    var ws = new WebSocket(`ws://localhost:3000/realtime-voteinfo/${id}`)
    var status = new Promise((res, rej) => {
      ws.addEventListener('open', e => {
        //本次投票的基本信息
        //数据包括 对象形式 vote基本信息  options选项信息  userVotes对应关系
        axios(`/vote/${id}`).then((res: any) => {
          console.log('基本信息', res)
          setInfo(res.data.result)
        }).catch((e:any) => {
          if (e.response.data.code === -1) {
            alert(e.response.data.msg)
          } else {
            throw(e)
          }
        })
        //绑定接收消息事件
        ws.addEventListener('message', e => {
          //收到的是该投票的实时更新情况，只要收到就更新
          //数据包括 选项id--用户id--avatar  数组形式
          setInfo((state) => {
            var userVotes = JSON.parse(e.data)
            console.log(userVotes)
            return {...state, userVotes}
          })
        })
        res(ws)
      })
      ws.addEventListener('error', e => {
        alert('网络错误，实时更新失败，请尝试刷新')
        rej(e)
      })
    })
    var ready = () => status
    return {
      ws,
      status,
      ready
    }
  }, [])


  function userClick(e: React.ChangeEvent<HTMLInputElement>) {
    var optionIds = [Number(e.target.value)]
    console.log(optionIds)
    axios.post(`/vote/${id}`, {
      optionIds
    }).catch((e: any) => {
      if (e.response.data.code === -1) {
        var errorinfo = e.response.data.msg
        if(errorinfo === 'user not login!'){
          confirm({
            title: '未登录用户无法投票，是否登录？',
            icon: <ExclamationCircleOutlined />,
            onOk() {
              navi('/login')
            },
            onCancel() {
              console.log('Cancel');
            },
          });
        }else{
          alert(e.response.data.msg)
        }
      }
    })
  }
  function anonymousVote() {
    var optionIds = Array.from(document.querySelectorAll('input:checked'))
      .map((it: any) => Number(it.value))
    axios.post(`/vote/${id}`, {
      optionIds
    }).catch((e: any) => {
      if (e.response.data.code === -1) {
        var errorinfo = e.response.data.msg
        if(errorinfo === 'user not login!'){
          confirm({
            title: '未登录用户无法投票，是否登录？',
            icon: <ExclamationCircleOutlined />,
            onOk() {
              navi('/login')
            },
            onCancel() {
              console.log('Cancel');
            },
          });
        }else{
          alert(e.response.data.msg)
        }
      }
    })
  }
  function spreadName(idx:number) {
    var list = $(`.avatarList.${idx}`)
    var button = list.children('.spread')
    if (list.hasClass('nameshow')) {
      list.removeClass('nameshow')
      button.text('[→')
    } else {
      list.addClass('nameshow')
      button.text('←]')
    }
  }
  return (
    voteinfo.vote === undefined
      ?
      <Spin />
      :
      <div className="theVote">
        <div className="header">
          <button onClick={() => {
            client.ready().then(() => {
              client.ws.close()
            })
            navi(-1)
          }}>返回</button>
        </div>
        <h2>{voteinfo.vote.title}</h2>
        <h3>{voteinfo.vote.desc}</h3>
        <h4>
          <span>{voteinfo.vote.multiple === 0 ? '单选' : '多选'}</span>
          <span>{ voteinfo.vote.deadline } 截止 </span>
        </h4>
        <div className="options">
          {
            voteinfo.options.map((it, idx) => {
              var user_option = voteinfo.userVotes.filter(el => el.optionId === it.optionId)
              var percent = user_option.length / voteinfo.userVotes.length * 100
              return (
                <div key={idx} className='theOption'>
                  {/* 内容 */}
                  {
                    voteinfo.vote.anonymous === 0
                      ?
                      <input
                        type={voteinfo.vote.multiple === 0 ? 'radio' : 'checkbox'}
                        name={voteinfo.vote.voteId}
                        id={'options-' + it.optionId}
                        value={it.optionId}
                        checked={currentUser !== null && user_option.some(it => currentUser?.match(it.name) !== null)}
                        onChange={userClick}
                        hidden
                      />
                      :
                      <input
                        type={voteinfo.vote.multiple === 0 ? 'radio' : 'checkbox'}
                        name={voteinfo.vote.voteId}
                        id={'options-' + it.optionId}
                        value={it.optionId}
                        defaultChecked={currentUser !== null && user_option.some(it => currentUser?.match(it.name) !== null)}
                        hidden
                      />
                  }
                  <label htmlFor={'options-' + it.optionId} >
                    <div className="optionInfo">
                      <span className="content">{it.content}</span>
                      <span className="select"></span>
                      <span className="voted">{user_option.length}</span>
                    </div>
                    <Progress percent={percent} showInfo={false} />
                  </label>
                  {/* 头像 */}
                  {
                    voteinfo.vote.anonymous === 0
                    &&
                    <div className={'avatarList ' + idx}>
                      {
                        user_option.map((person,idx) => {
                          return <div key={idx}>
                            <Avatar size="small" src={person.avatar === null ? '/upload/default.jpg' : person.avatar} />
                            <span className="name">{person.name}</span>
                          </div>
                        })
                      }
                      {
                        user_option.length > 0
                        &&
                        <button className="spread"
                          onClick={() => { spreadName(idx) }}
                          style={{
                            border: '1px solid lightgrey',
                            borderRadius: '50%'
                          }}
                        >
                          {'[→'}
                        </button>
                      }
                    </div>
                  }
                </div>
              )
            })
          }
        </div>
        {
          voteinfo.vote.anonymous === 1
          &&
          <Button
              type="primary"
              size='large'
              style={{
                width: '80%',
                margin: '20px 10%'
              }}
              // 当前用户登录了，并且已经投票，则禁用
              disabled={currentUser !== null && voteinfo.userVotes.some(it => currentUser?.match(it.name) !== null )}
              onClick={anonymousVote} >投票</Button>
        }
      </div>
  )
}

export default TheVote