import { useParams } from "react-router-dom"
import { useNavigate } from "react-router-dom"
import { Button, Form, Input, DatePicker, Switch } from 'antd';
import { MinusCircleOutlined, PlusOutlined } from '@ant-design/icons';
import 'antd/dist/antd.css';
import FormItem from "antd/lib/form/FormItem";
import FormList from "antd/lib/form/FormList";
import './Vote.css'

const axios = require('axios');
const moment = require('moment');


const Vote = () => {
  var param = useParams()
  var method = param.style
  const navi = useNavigate()
  var currentTime = moment()
  var deadlineTime = [currentTime.year() + 1, currentTime.month() + 1, currentTime.date()].join('-') + 
    ' ' + [currentTime.hour(), currentTime.minute()].join(':') //默认截止日期为一年后
  interface voteinfo {
    title: string, //标题
    desc: string, //补充说明
    options: string[], //投票选项
    deadline: string, //截止日期
    anonymous: boolean, //匿名
    multiple: boolean //是否多选
  }
  function finish(values: any) {
    console.log(values)
    var res = {
      title: values.title,
      desc: values.qs,
      options: values.options,
      deadline: values.dl._i,
      anonymous: values.covername,
      multiple: method === 'single' ? false : true
    } as voteinfo
    axios.post('/vote', res).then((res: any) => {
      console.log(res)
      navi(`/vote/${res.data.result.voteId}`)
    }).catch((e: any) => {
      if (e.response.data.code === -1) {
        if (window.confirm('对不起，未登录无法发布投票，是否登录？')) {
          navi('/login')
        } else {
          navi('/')
        }
      } else {
        throw(e)
      }
    })
  }


  return (
    <div className="vote">
      <h1>
        <button className="back" onClick={() => { navi('/') }}>{'返回'}</button>
        {
          method === 'single'
            ?
            '创建单选投票'
            :
            '创建多选投票'
        }
      </h1>
      <Form onFinish={finish} style={{padding: '0px 30px'}} >
        <FormItem name='title' rules={[{required: true, message:'标题不能为空'}]}>
          <Input placeholder="请输入标题" style={{fontSize: '30px', fontWeight: 'bold'}} />
        </FormItem>
        <FormItem name='qs'>
          <Input placeholder="补充描述（选填）" style={{fontSize: '24px'}} />
        </FormItem>
        <FormList name='options' initialValue={['','']}>
          {
            (fields, { add, remove }, { errors }) => (
              <div className="options">
                {
                  fields.map((field, idx) => {
                    return (
                      <FormItem key={idx}
                        wrapperCol={{span: 20}}
                        style={{
                          marginBottom: '0px'
                        }}
                      >
                        {
                          fields.length > 2
                          &&
                          <MinusCircleOutlined
                            onClick={() => { remove(field.name) }}
                            style={{
                              color: '#FF5353',
                              float: 'left'
                            }} />
                        }
                        <FormItem {...field} style={{marginBottom: '10px'}} rules={[{required: true, message: '选项不能为空'}]}>
                          <Input placeholder="选项" style={{fontSize: '24px', height: '50px'}}/>
                        </FormItem>
                      </FormItem>
                    )
                  })
                }
                <FormItem>
                  <Button
                    type="dashed"
                    onClick={() => { add() }}
                    style={{color: '#256DF3' }}
                    icon={<PlusOutlined />}
                    size='large'
                  >
                    添加选项
                  </Button>
                </FormItem>
              </div>
            )
          }
        </FormList>
        <Form.Item name="dl" label="截止日期"
          wrapperCol={{ offset: 10 }}
          style={{
            marginTop: '100px'
          }}
          initialValue={moment(deadlineTime)}
        >
          <DatePicker showTime
            format="YYYY-MM-DD HH:mm:ss"
            // onChange={console.log}
            style={{ float: 'right' }}
          />
        </Form.Item>
        <Form.Item
          name="covername"
          label="匿名投票"
          wrapperCol={{ offset: 10 }}
          valuePropName='checked'
          initialValue={false}
        >
          <Switch style={{float: 'right'}} />
        </Form.Item>
        <Form.Item wrapperCol={{ offset: 9 }}>
          <Button type="primary" htmlType="submit"
            style={{fontSize: '30px', height: '60px', position: 'fixed', bottom: '50px'}}
          >
            创建投票
          </Button>
        </Form.Item>
      </Form>
    </div>
  )
}

export default Vote